const withHotkeys = (constructor) => class extends constructor {
    constructor(albums, params) {
        super(albums, params);
        document.addEventListener('keydown', (e) => {
            switch (e.key) {
                case 'ArrowLeft':
                    this.prev();
                    break;
                case 'ArrowRight':
                    this.next();
                    break;
            }
        });
    }
};
export default withHotkeys;
